'use strict'

import EL from '../constant/elements'
import gsap from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger);
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
gsap.registerPlugin(ScrollToPlugin);


export default () => {

    const mediaQuery = window.matchMedia('(min-width: 1025px)');
 
    // 
    handle(mediaQuery);
    
    // 
    mediaQuery.addListener(handle);
    
    function handle(mm) {
    if (mm.matches) {
        const wrapper = document.querySelector('#wrapper');
        if(wrapper) {
            const panels = gsap.utils.toArray('.panel');
            const wrapperWidth = wrapper.offsetWidth;
        
            let scrollTween = gsap.to( panels, {
                xPercent: -100 * (panels.length - 1),
                ease: "none",
                animation: scrollTween,
                scrollTrigger: {
                    trigger: wrapper,
                    pin: true,
                    scrub: 1,
                    // snap: { // 
                    //     snapTo: 1 / ( panels.length - 1 ), // 
                    //     duration: {min: .4, max: .6}, // 
                    //     ease: "none" // easing
                    // },
                    end: () => "+=" + wrapperWidth // 
                }
            })
    
            // 
            const anchors = document.querySelectorAll(".anchor");
            let index = '';
            anchors.forEach( (anchor) => {
                anchor.addEventListener( 'click', (e) => {
                    e.preventDefault();
                    index = [].slice.call(anchors).indexOf(anchor); // 
                    const target = document.querySelector(e.currentTarget.querySelector('a').getAttribute('href')); // panel
                    const scrollbarWidth = window.innerWidth - document.body.clientWidth; // 
                    const wrapperOffset = target.offsetLeft * ( wrapper.clientWidth / ( wrapper.clientWidth - window.innerWidth ) ) + scrollbarWidth * index; // 
                    gsap.to(window, {
                        scrollTo: {
                            y: wrapperOffset,
                            autoKill: false
                        },
                        duration: 1
                    });
                });
            });
    
            //  fade in 
            const images = document.querySelectorAll('.js-fadeup')
            images.forEach(el => {
                gsap.from(el, 
                {
                    autoAlpha: 0,
                    y: 100,
                    scrollTrigger: {
                        trigger: el,
                        start: "right right",
                        end: "right 70%",
                        scrub: 1,
                        // markers:true,
                        containerAnimation:scrollTween,
                    },
                });
            });
    
            //  blur
            const blurs = document.querySelectorAll('.js-blur')
            blurs.forEach(el => {
                gsap.from(el, 
                {
                    scrollTrigger: {
                        trigger: el,
                        start: "left right",
                        scrub: 1,
                        // markers:true,
                        containerAnimation:scrollTween,
                        toggleClass: {targets: el, className: "is-active"},
                    },
                });
            });
    
            // 
            const reversal = document.querySelector('.js-reversal')
            gsap.from(reversal, 
                {
                    ease: "none",
                    opacity:1,
                    scrollTrigger: {
                     trigger: reversal,
                     start: "left 70%",
                     end: "right -50%",
                     scrub: 1,
                     containerAnimation:scrollTween,
                    //  markers: true,
                     toggleClass: {targets: EL.BODY , className: "is-active"},
                    },
                });
    
            // 
            const etcimages = document.querySelectorAll('.js-etcimg')
            etcimages.forEach(el => {
                // gsap.set(el, {y:-500})
                gsap.from(el, 
                {
                    y: 500,
                    scrollTrigger: {
                        trigger: ".top-etc",
                        start: "left right",
                        end: "right left",
                        scrub: 1,
                        containerAnimation:scrollTween,
                    },
                });
            });
            gsap.from(".js-etcimg02", 
                {
                    y: -500,
                    scrollTrigger: {
                        trigger: ".top-etc",
                        start: "left right",
                        end: "right left",
                        scrub: 1,
                        containerAnimation:scrollTween,
                    },
            });
    
            // SNS fadein
            gsap.set("#js-sns01", { y: 30, autoAlpha: 0});
            gsap.set("#js-sns02", { y: 30, autoAlpha: 0});
            gsap.set("#js-sns03", { y: 30, autoAlpha: 0});
            gsap.timeline({
                defaults: {
                  duration: 1,
                  ease: "easeInOut"
                },
              
                scrollTrigger: {
                  trigger: "#js-snstrigger",
                  start: "left right",
                  toggleActions: "play none none reset",
                  containerAnimation:scrollTween,
                //   markers: true,
                }
              })
            .to("#js-sns01", { y: 0, autoAlpha: 1 ,duration: .8 })
            .to("#js-sns02", { y: 0, autoAlpha: 1 ,duration: .8 }, "-=.5")
            .to("#js-sns03", { y: 0, autoAlpha: 1 ,duration: .8 }, "-=.5")
        }

            // mv 
        const mvparallax01 = document.querySelector('.js-mvparallax01');
        const mvparallax02 = document.querySelector('.js-mvparallax02');
        const mvparallax03 = document.querySelector('.top-mv__title');

        gsap.fromTo(mvparallax01, {
        x: 0,
        }, {
        x: 150, 
        ease: "none",
        scrollTrigger: {
        trigger: '#panel01',
        start: "bottom bottom",
        end: "bottom top",
        scrub: 1,
        }
        });
        gsap.fromTo(mvparallax02, {
            x: 0,
        }, {
            x: 100, 
            ease: "none",
            scrollTrigger: {
            trigger: '#panel01',
            start: "bottom bottom",
            end: "bottom top",
            scrub: 1,
            //  markers: true,
            }
        });
        gsap.fromTo(mvparallax03, {
            x: 0,
        }, {
            x: 200, 
            ease: "none",
            scrollTrigger: {
            trigger: '#panel01',
            start: "bottom bottom",
            end: "bottom top",
            scrub: 1,
            //  markers: true,
            }
        });

    } else {
        // 

        //  blur
        const blurs = document.querySelectorAll('.js-blur')
        blurs.forEach(el => {
            gsap.from(el, 
            {
                scrollTrigger: {
                    trigger: el,
                    start: "top 80%",
                    // markers:true,
                    toggleClass: {targets: el, className: "is-active"},
                },
            });
        });
    }
    }

    // 
    const progressBar = document.querySelector('.progress__bar');
    window.addEventListener('scroll', () => {
    const windowYPos = window.pageYOffset;
    const height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
    const scrolled = (windowYPos / height) * 100;
    progressBar.style.width = scrolled + "%";
    });


}
